import React, { useEffect, useRef, useState } from "react";
import { FullPage, Slide } from "react-full-page";
import Typed from "react-typed";
import { makeStyles } from "@mui/styles";
import Skill from "../components/Skill";
import TimeLine from "../components/TimeLine";
import IconButton from "@mui/material/IconButton";
import KeyboardDoubleArrowDownIcon from "@mui/icons-material/KeyboardDoubleArrowDown";
import Typography from "@mui/material/Typography";
import Project from "../components/Project";

const useStyles = makeStyles(() => ({
  body: {
    display: "flex",
    flexFlow: "column",
    justifyContent: "center",
    // alignItems: "center",
  },
  arrowButton: {
    // transform: "rotate(0deg)",
    // transition: "transform 0.2s ease",
    position: "relative", // position 
    overflow: "visible", // overflow 
    // flexFlow: "column",
  },
  arrowIcon: {
    position: "absolute",
    top: 0, //   
    left: 0,
    animation: "$bounce 1s ease-in-out infinite", //  
  },
  arrowButtonExpanded: {
    transform: "rotate(180deg)",
  },
  "@keyframes bounce": {
    //   
    "0%, 100%": {
      top: 0,
    },
    "50%": {
      top: "-10px",
    },
  },
  reply: {
    textAlign: "center",
  },
  project: {
    display: "flex",
    flexFlow: "column",
    justifyContent: "center",
    height: "auto !important",
  },
}));

export default function Main(props) {
  const { fullPageRef, value, beforeChange } = props;
  const [isExpanded, setIsExpanded] = useState(false);
  const classes = useStyles();
  const typedRef = useRef(null);
  const [activeTyped, setActiveTyped] = useState(false);

  const handleExpandClick = () => {
    setIsExpanded(!isExpanded);
    setActiveTyped(true);
  };

  // useEffect(() => {
  //   const timer = setInterval(() => {
  //     // setActiveTyped(false);
  //   }, 1000);
  //   return () => {
  //     clearInterval(timer);
  //   };
  // }, []);

  return (
    <FullPage ref={fullPageRef} beforeChange={beforeChange} scrollMode="normal">
      <Slide value="1" className={classes.body}>
        <Typed
          strings={[
            ".",
            "   ",
            "   ",
            "   .",
          ]}
          typeSpeed={40}
          backSpeed={50}
          attr="placeholder"
          smartBackspace
          showCursor={true}
          cursorChar="|"
          ref={typedRef}
          loop={activeTyped && true}
        >
          <input
            type="text"
            style={{
              background: "none",
              border: "none",
              color: "#fff",
              width: "100%",
              fontSize: "1.6rem",
              textAlign: "center",
            }}
          />
        </Typed>

        <IconButton
          aria-label="delete"
          className={`${classes.arrowButton} ${
            isExpanded ? classes.arrowButtonExpanded : ""
          }`}
          onClick={handleExpandClick}
        >
          <Typography
            variant="caption"
            display="block"
            gutterBottom
            className={classes.reply}
          >
            
          </Typography>
          <KeyboardDoubleArrowDownIcon id="arrow-icon" />
        </IconButton>
      </Slide>
      <Slide value="2" className={classes.body}>
        <Skill />
      </Slide>
      <Slide value="3" className={classes.body}>
        <TimeLine />
      </Slide>
      <Slide value="4" className={classes.project}>
        <Project />
      </Slide>
      <Slide value="5" className={classes.body}>
        5
      </Slide>
    </FullPage>
  );
}
